@import "../constants/mixins";
@import "../constants/variables";

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: $scrollbar-color;
  border-radius: 10px;
  width: 5px;
  height: 120px;
  &:hover {
    background-color: $scrollbar-color--active;
  }
}
